import React, { useState } from "react";
import Header from './components/Molecules/Header/AppHeader'
import DashboardPage from './components/Pages/DashboardPage'
import MyStoriesPage from './components/Pages/MyStoriesPage'
import BlogDetailPage from './components/Pages/BlogDetailPage'
import LandingPage from './components/Pages/LandingPage'
import NewBlogPage from './components/Pages/NewBlogPage'
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { useAuth0 } from "@auth0/auth0-react";

function App() {
  const { isAuthenticated } = useAuth0();
  const { user } = useAuth0();
  return (
    <React.Fragment>
      <Router>
        <Header isAuthenticated={isAuthenticated}  currentUserName={user}/>
        {
          !isAuthenticated ?
            <LandingPage /> :
            <Switch>
              <Route path="/" exact component={DashboardPage} />
              <Route path="/dashboard" exact component={DashboardPage} />
              <Route path="/dashboard/:email" component={DashboardPage} />
              <Route path="/blogdetail/:postId" component={BlogDetailPage} />
              <Route path="/new" component={NewBlogPage} />
              <Route path="/mystories"  component={MyStoriesPage} />
              

            </Switch>
        }

      </Router>
    </React.Fragment>
  );
}

export default App;
