import React, { useState, useEffect } from 'react'
import BlogDetailGridLayout from '../Templates/BlogDetailGridLayout'
import axios from 'axios';

function BlogDetailPage({ match }, props) {

    const [blog, setBlog] = useState([]);
    const [loading, setLoading] = useState(true);

    const [users, setUsers] = useState({});
    const [twitterUrl, setTwitterUrl] = useState('');
    const [avatarLink, setAvatarLink] = useState('');

    useEffect(() => {
        getData();
        
    }, []);

    const getData = () => {
        setLoading(true);
        axios.get('/blogApi')
            .then((response) => {
                const data = response.data;
                const blogItem = data.filter((item) => {
                    return item.postId === match.params.postId
                });
                setBlog(blogItem);
                getUserDetail(blogItem[0].postedBy);
                setLoading(false);
            })
            .catch((error) => {
                console.log(error);
            });
    }

    const getUserDetail = (userEmail) => {

        axios.get('/users', {
            params: {
                userEmail: userEmail
            }
        })
            .then((response) => {

                const data = response.data;
                setUsers(data);
                console.log(data);
                setTwitterUrl(data.twitterUrl);
                setAvatarLink(data.avatarLink);
            })
            .catch((error) => {
                console.log(error);
            });
    }



    return (
        <div>
            <BlogDetailGridLayout blog={blog} />
        </div>
    )
}

export default BlogDetailPage
