import React, { useEffect, useState } from "react";
import Card from '../Organisms/Card/Card'
import BlogPostLayout from '../Templates/BlogPostLayout'
import axios from 'axios';
import { makeStyles } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import { useAuth0 } from "@auth0/auth0-react";
import { getRndInteger } from '../Particles/utils/mathUtils'

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        '& > * + *': {
            marginLeft: theme.spacing(3),
        },
    },
}));

function DashboardPage({ match }) {


    const { user } = useAuth0();
    
    const getBlogPost = (postId) => {
        axios.get('/blog', {
            params: {
                postId: postId
            }
        })
            .then((response) => {
                const data = response.data;
                let retrievedBlog = data[0];
                let currentLikeCount = retrievedBlog.clapCount;
                let newLikeCount = Number(currentLikeCount)+1;
                updateTheBlog(postId,newLikeCount,retrievedBlog);
            })
            .catch((error) => {
                console.log(error);
            });
    }


    const updateTheBlog = (postId,newLikeCount,b)=>{
        b.clapCount=newLikeCount;
        postTheBlog(b);
    }

    const postTheBlog=(blog)=>{

        const payload = {
            postId: blog.postId,
            postedBy: blog.postedBy,
            title: blog.title,
            body: blog.body,
            cardImage: blog.cardImage,
            subHeader: blog.subHeader,
            avatarText: blog.avatarText,
            responseCount: blog.responseCount,
            clapCount: blog.clapCount,
            cardContentVariant: "body2",
            cardContentHeading: blog.title,
            likes:[{
                likeId: getRndInteger(100, 9999),
                postId: blog.postId,
                liked: "yes",
                likedBy: user["email"]
            }],
            content: blog.body,
            color: "textSecondary"
        };

        axios({
            url: '/blogApi/update',
            method: 'PUT',
            data: payload
        })
            .then(() => {
                console.log("updated the blog")
            })
            .catch(() => {
                console.log('Internal server error');
            });



    }

    const likeThePost = (postId) => {
        const payload = {
            likeId: getRndInteger(100, 9999),
            postId: postId,
            liked: "yes",
            likedBy: user["email"]
        };
        axios({
            url: '/like/save',
            method: 'POST',
            data: payload
        })
            .then(() => {
                console.log("liked the post with post id" + postId)
            })
            .catch(() => {
                console.log('Internal server error');
            });
    }

    const getTheLikesForPost = (postId) => {
        axios.get('/likes', {
            params: {
                postId: postId
            }
        })
            .then((response) => {
                const data = response.data;
                if (response.data.length === 0)
                    setInterestedLikesObj({ [postId]: [] });
                else
                    setInterestedLikesObj({ [postId]: data });
            })


            .catch((error) => {
                console.log(error);
            });
    }

    const likeThisPost = (postId) => {

        getBlogPost(postId);
       
    }

    const commentPost = (postId) => {

        const isOpen = !isCommentSecOpen;
        setIsCommentSecOpen(isOpen);
        setCurrentpostId(postId);
    }

    const handleCommentCapture = (e) => {
        setCommentForCurrentPost(e.target.value);
    }
    const fetchCommentsData = (postId) => {
        axios.get('/comments', {
            params: {
                postId: postId
            }
        })
            .then((response) => {
                const data = response.data;
                if (response.data.length === 0)
                    setInterestedCommentsObj({ [postId]: [] });
                else
                    setInterestedCommentsObj({ [postId]: data });
            })
            .catch((error) => {
                console.log(error);
            });
    }
    const fetchCommentsForThisPost = (postId) => {
        setIsViewCommentSecOpenValue(!isViewCommentSecOpenValue);
        setIsViewCommentSecOpen({ [postId]: isViewCommentSecOpenValue });
        fetchCommentsData(postId);
    }

    const resetUserInputs = () => {
        setCommentForCurrentPost('');
        setIsCommentSecOpen(false);
    }

    const postTheComment = () => {

        const payload = {
            commentId: getRndInteger(100, 9999),
            postId: currentpostId,
            comment: commentForCurrentPost,
            commentedBy: user["email"]
        };


        axios({
            url: '/comment/save',
            method: 'POST',
            data: payload
        })
            .then(() => {
                resetUserInputs();
                fetchCommentsForThisPost(currentpostId);
            })
            .catch(() => {
                console.log('Internal server error');
            });
    }


    const classes = useStyles();


    const [blogs, setBlogs] = useState([]);
    const [loading, setLoading] = useState(true);
    const [isCommentSecOpen, setIsCommentSecOpen] = useState(false);

    // const [isViewCommentSecOpen, setIsViewCommentSecOpen] = useState(false);
    const [isViewCommentSecOpenValue, setIsViewCommentSecOpenValue] = useState(false);

    const [isViewCommentSecOpen, setIsViewCommentSecOpen] = useState({});
    const [interestedComments, setInterestedComments] = useState([]);
    const [interestedCommentsObj, setInterestedCommentsObj] = useState({});


    const [interestedLikesObj, setInterestedLikesObj] = useState({});

    const [currentpostId, setCurrentpostId] = useState('');
    const [commentForCurrentPost, setCommentForCurrentPost] = useState('');

    const [currentBlog, setCurrentBlog] = useState([]);

    useEffect(() => {
        //just to delay the API call, not really required..
        setTimeout(() => {
            getData();
        }, 200);

    }, []);

    const getData = () => {

        setLoading(true);
        axios.get('/blogApi')
            .then((response) => {
                const data = response.data;
                setLoading(false);
                setBlogs(data);

            })
            .catch((error) => {
                console.log(error);
            });
    }
    return (
        <React.Fragment>

            <BlogPostLayout>
                {(blogs.length > 0 && !loading) ?
                    blogs.map((blog, index) =>
                        <Card
                            key={blog.postId}
                            postId={blog.postId}
                            cardHeaderTitle={blog.postedBy}
                            subHeader={blog.subHeader}
                            cardHeaderAvatarString={blog.avatarText}
                            responseCount={blog.responseCount}
                            cardMedia={blog.cardImage}
                            clapCount={blog.clapCount}
                            cardContentVariant={blog.cardContentVariant}
                            cardContentHeading={blog.cardContentHeading}
                            cardContent={blog.content}
                            cardContentColor={blog.color}

                            commentPost={commentPost}
                            handleCommentCapture={handleCommentCapture}
                            postTheComment={postTheComment}

                            isCommentSecOpen={isCommentSecOpen}

                            isViewCommentSecOpen={isViewCommentSecOpen}
                            fetchCommentsForThisPost={fetchCommentsForThisPost}
                            interestedCommentsObj={interestedCommentsObj}


                            likeThisPost={likeThisPost}
                            interestedLikesObj={interestedLikesObj}

                        />
                    )
                    : null
                }
                {
                    (blogs.length <= 0 && !loading) ?
                        "No Blogs from this author !" : null
                }
            </BlogPostLayout>
            {
                loading ?
                    <CircularProgress />
                    : null
            }

        </React.Fragment>
    )
}

export default DashboardPage












